import React from "react";

function Matopeli() {
    if (window.innerWidth >= 1024) {
        return (
            <>
                <h2 id="jumpToContent" style={{ fontWeight: 600, fontFamily: 'Honk', letterSpacing: '4px', fontSize: "3em" }}>MATOPELI</h2>
                <p>Ensimminen oma projekti oli nostalginen matopeli vrillisen ja valmistui alkukevll 2023. (Pelataksesi klikkaa peliruutua ja ohjaa nuolinppimill). Madon vauhti kasvaa asteittain.</p><p>Harjoittelun kohteena: Javascript, HTML, CSS</p>
                <iframe title="Matopeli" src={`${process.env.PUBLIC_URL}/CrazyWormGameFinal/index.html`}></iframe>
                <p>Pelin repositorion lydt tlt: <a className="githubLink" href="https://github.com/AnAr854/WormGame" target="_blank" rel="noreferrer">Matopelin repositorio</a></p>
            </>
        )
    } else if (window.innerWidth < 1024) {
        return (
            <>
                <h2 id="jumpToContent" style={{ fontWeight: 600, fontFamily: 'Honk', letterSpacing: '2px' }}>MATOPELI</h2>
                <p>Ensimminen oma projekti oli nostalginen matopeli vrillisen ja valmistui alkukevll 2023. Peli voi kokeilla tietokoneen nppimistll, yli 1024px:i leveill nytill.</p><p>Harjoittelun kohteena: Javascript, HTML, CSS</p>
                <img src={require('../../src/img/wormGameImg.png')} id="wormGameImg" className="box_shadow" alt="gameplay" />
                <p>Pelin repositorion lydt tlt: <a className="githubLink" href="https://github.com/AnAr854/WormGame" target="_blank" rel="noreferrer">Matopelin repositorio</a></p>
            </>
        )
    }
}

export default Matopeli;